import Vue from 'vue'
import Router from 'vue-router'

const mnagement = r => require.ensure([], () => r(require('@/pages/mnagement')), 'mnagement')
const roleManagement = r => require.ensure([], () => r(require('@/pages/roleManagement')), 'roleManagement')
const userMnagement = r => require.ensure([], () => r(require('@/pages/userMnagement')), 'userMnagement')
const recruitmentManagement = r => require.ensure([], () => r(require('@/pages/recruitmentManagement')), 'recruitmentManagement')
const addAccountNumber = r => require.ensure([], () => r(require('@/pages/addAccountNumber')), 'addAccountNumber')
const roleManager = r => require.ensure([], () => r(require('@/pages/roleManager')), 'roleManager') //角色管理
const apiUser = r => require.ensure([], () => r(require('@/pages/apiUser')), 'apiUser')  //Api用户管理
const addApiUser = r => require.ensure([], () => r(require('@/pages/addApiUser')), 'addApiUser')//新增或修改用户管理
const fileUpload = r => require.ensure([], () => r(require('@/pages/fileUpload')), 'fileUpload')//文件上传
const newsRecommendation = r => require.ensure([], () => r(require('@/pages/newsRecommendation')), 'newsRecommendation')//首页新闻推荐


//登录页面
const login = r => require.ensure([], () => r(require('@/components/login')), 'login')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',// 登录
      name: 'login',
      component: login
     },
    {
      path:'/mnagement', name:"mnagement",component:mnagement,
      meta:{requireAuth: true,},
      redirect: '/userMnagement',
      children:[
        {
          path:'/userMnagement', name:"userMnagement",component:userMnagement,meta:{requireAuth: true,},
    
        }, {
          path:'/roleManagement', name:"roleManagement",component:roleManagement,meta:{requireAuth: true,},
        },
        {
          path:'/recruitmentManagement',name:'recruitmentManagement',component:recruitmentManagement,meta:{requireAuth: true,},
        },
        {
          path:'/addAccountNumber',name:'addAccountNumber',component:addAccountNumber,meta:{requireAuth: true,},
        },{
          path:'/roleManager',name:'roleManager',component:roleManager,meta:{requireAuth: true,},
        },{
          path:'/apiUser',name:'apiUser',component:apiUser,meta:{requireAuth: true,},
        },{
          path:'/addApiUser',name:'addApiUser',component:addApiUser,meta:{requireAuth: true,},
        },{
          path:'/fileUpload',name:'fileUpload',component:fileUpload,meta:{requireAuth: true,},
        },{
          path:'/newsRecommendation',name:'newsRecommendation',component:newsRecommendation,meta:{requireAuth: true,},
        },
        
      ],
      
    },
    // {
    //   path:'/login',
    //   name:'login',
    //   component:login
    // }
  ]
})



